:root {
  --font-family: "FuturaNew", sans-serif;
  --content-width: 1750px;
  --content-width--wide: 1640px;
  --content-width--narrow: 1350px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width--wide: calc(var(--content-width--wide) + (var(--container-offset) * 2));
  --container-width--narrow: calc(var(--content-width--narrow) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #080606;
  --main-color-dark: #59443f;
  --main-color-light: #ffc59e;
  --grey-color: #c3b9b5;
  --grey-light-color: #ede9e8;
  --bg-light: #ddd4f3;
  --bg-color: #fbf8f8;
  --main-fz: 20px;
  --phone-fz: 16px;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Bold.woff2") format("woff2"), url("../FuturaNew-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "SchoolBook";
  src: url("../fonts/SchoolBook-Regular.woff2") format("woff2"), url("../SchoolBook-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Regular.woff2") format("woff2"), url("../FuturaNew-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html,
body {
  height: 100%;
}

body {
  color: var(--main-color-dark);
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page {
  font-family: var(--font-family, sans-serif);
  font-size: var(--main-fz);
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}
@media only screen and (max-width: 559px) {
  .page {
    font-size: var(--phone-fz);
  }
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.container--content-wide {
  max-width: var(--container-width--wide);
}

.container--content-narrow {
  max-width: var(--container-width--narrow);
}

.logo {
  flex-shrink: 0;
}
.logo__img {
  object-fit: contain;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox-hidden,
.radio-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--main-color-dark);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}
@media only screen and (max-width: 767px) {
  .burger {
    display: block;
  }
}

.btn {
  border-radius: 30px;
  padding: 24px 77px;
  color: var(--light-color);
  text-transform: uppercase;
  font-weight: 700;
}
.btn--brown {
  background-color: var(--main-color);
  box-shadow: 0px 4px 20px var(--main-color);
}
.btn--grey {
  background-color: var(--grey-light-color);
  box-shadow: 0px 4px 20px var(--grey-light-color);
}
.btn--up {
  background-color: var(--light-color);
  border-radius: 50px;
  color: var(--main-color);
  font-weight: 400;
  padding: 11px 46px;
}

.app-box {
  display: flex;
  align-items: center;
}
.app-box__link {
  flex-shrink: 0;
}
.app-box__link:not(:last-child) {
  margin-right: 20px;
}
.app-box__img {
  height: 40px;
  object-fit: cover;
}

.social-list {
  display: flex;
  justify-content: space-between;
  max-width: 310px;
  width: 100%;
}
.social-list__item:not(:last-child) {
  margin-right: 10px;
}
.social-list__item svg {
  width: 40px;
  height: 40px;
  fill: var(--bg-light);
  transition: fill 0.3s ease 0s;
}
.social-list__item svg:hover {
  fill: var(--grey-color);
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 10px;
}
.contacts-list__link {
  transition: color 0.3s ease 0s;
}
.contacts-list__link:hover {
  color: var(--grey-color);
}

.modal-city {
  display: flex;
  align-items: center;
  color: var(--main-color-light);
}
.modal-city:hover {
  color: var(--main-color-dark);
}
.modal-city__btn {
  display: block;
  text-transform: uppercase;
  padding: 10px 0 10px 10px;
  transition: color 0.3s ease 0s;
}
.modal-city__btn:hover {
  color: var(--main-color-dark);
}
@media only screen and (max-width: 559px) {
  .modal-city__btn {
    font-size: 14px;
  }
}
.modal-city__icon {
  display: block;
  width: 20px;
  height: 30px;
  fill: currentColor;
  transition: color 0.3s ease 0s;
}
@media only screen and (max-width: 559px) {
  .modal-city__icon {
    width: 15px;
    height: 25px;
  }
}

.header {
  margin-top: 65px;
}
@media only screen and (max-width: 1229px) {
  .header {
    margin-top: 40px;
  }
}
@media only screen and (max-width: 767px) {
  .header {
    margin-top: 0;
  }
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  font-weight: 700;
}
@media only screen and (max-width: 1229px) {
  .header__nav {
    font-size: 18px;
  }
}
@media only screen and (max-width: 991px) {
  .header__nav {
    font-size: 16px;
  }
}
.header__burger {
  z-index: 2;
}

.menu {
  color: var(--grey-color);
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 991px) {
  .menu {
    margin-right: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .menu {
    position: absolute;
    background-color: var(--main-color-light);
    padding-top: 100px;
    margin-right: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    flex-direction: column;
    transform: translateY(-200%);
    transition: transform 0.4s;
    z-index: 1;
    overflow: hidden;
  }
}
.menu__item {
  margin: 0 35px;
  text-align: center;
}
@media only screen and (max-width: 1229px) {
  .menu__item {
    margin: 0 20px;
  }
}
@media only screen and (max-width: 767px) {
  .menu__item {
    color: var(--main-color-dark);
    width: 100%;
  }
  .menu__item:not(:last-child) {
    margin: 0 0 20px 0;
  }
}
.menu__link {
  padding: 10px 0;
  transition: color 0.3s ease 0s;
}
.menu__link:hover {
  color: var(--main-color-dark);
}
@media only screen and (max-width: 767px) {
  .menu__btns {
    display: flex;
    align-items: center;
  }
}
@media only screen and (max-width: 767px) {
  .menu__modal-city {
    margin-right: 20px;
  }
}
@media only screen and (max-width: 559px) {
  .menu__modal-city {
    margin-right: 15px;
  }
}
.menu--active {
  transform: translateY(0);
}

.footer {
  background-color: var(--main-color-dark);
  color: var(--light-color);
  padding: 80px 0 60px;
}
@media only screen and (max-width: 559px) {
  .footer {
    padding: 40px 0 30px;
  }
}
.footer__logo {
  margin-right: 20px;
}
@media only screen and (max-width: 479px) {
  .footer__logo {
    display: block;
    margin-right: 0;
    margin-bottom: 20px;
  }
}
@media only screen and (max-width: 559px) {
  .footer__logo .logo__img {
    width: 250px;
    height: 60px;
  }
}
.footer__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
@media only screen and (max-width: 767px) {
  .footer__top {
    display: block;
  }
}
.footer__app-box {
  flex-shrink: 0;
}
@media only screen and (max-width: 767px) {
  .footer__app-box {
    max-width: 100%;
    justify-content: center;
  }
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 991px) {
  .footer__bottom {
    display: block;
  }
}
.footer__box-links {
  display: flex;
  justify-content: space-between;
  max-width: 650px;
  margin-right: 30px;
  width: 100%;
}
@media only screen and (max-width: 991px) {
  .footer__box-links {
    max-width: 100%;
    margin-bottom: 30px;
  }
}
@media only screen and (max-width: 479px) {
  .footer__box-links {
    display: block;
  }
}
.footer__item-list:not(:last-child) {
  margin-right: 20px;
}
@media only screen and (max-width: 479px) {
  .footer__item-list:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.footer__politics, .footer__item-list {
  max-width: 150px;
  width: 100%;
}
@media only screen and (max-width: 991px) {
  .footer__politics, .footer__item-list {
    max-width: 200px;
  }
}
.footer__politics {
  display: block;
  margin-right: 30px;
}
@media only screen and (max-width: 479px) {
  .footer__politics {
    margin-right: 0;
    margin-bottom: 20px;
    max-width: 100%;
    width: 100%;
  }
}
.footer__link, .footer__politics {
  transition: color 0.3s ease 0s;
}
.footer__link:hover, .footer__politics:hover {
  color: var(--grey-color);
}
.footer__box-social {
  max-width: 310px;
  width: 100%;
}
@media only screen and (max-width: 991px) {
  .footer__box-social {
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }
}
.footer__social-list {
  margin-bottom: 30px;
}
@media only screen and (max-width: 991px) {
  .footer__social-list {
    margin-bottom: 0;
    align-self: center;
    order: 1;
  }
}
@media only screen and (max-width: 991px) {
  .footer__contacts-list {
    display: flex;
    justify-content: space-around;
    align-self: center;
    margin-bottom: 30px;
    max-width: 100%;
    width: 100%;
    order: -1;
  }
}
@media only screen and (max-width: 479px) {
  .footer__contacts-list {
    display: block;
  }
}